.elements {
  max-width: 880px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, 282px);
  column-gap: 17px;
  row-gap: 20px;
  padding-top: 51px;
  justify-content: center;
}

@media screen and (max-width: 879px) {
  .elements {
    padding-top: 36px;
  }
}
